/* BoardList.css */
.board-list {
  width: 100%;
  margin: 20px 0;
}

.board-table {
  width: 100%;
  border-collapse: collapse;
}

.board-table th,
.board-table td {
  border: 1px solid #ddd;
  padding: 8px;
}

.board-table th {
  background-color: #f2f2f2;
  text-align: left;
}

.board-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.board-table tr:hover {
  background-color: #ddd;
}

.board-table td {
  text-align: center;
}
