/* BoardSearchBox.css */
.board-search-container {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  position: relative; /* 검색창의 위치 조정을 위해 relative 설정 */
}

.board-search-form {
  display: flex;
  position: absolute; /* 검색창을 부모 요소 기준으로 배치 */
  right: 520px; /* 검색창을 오른쪽으로 이동 */
}

.board-search-input {
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px 0 0 4px;
  outline: none;
  width: 300px;
}

.board-search-button {
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.board-search-icon {
  width: 24px;
  height: 24px;
}
